export const getPosts = `{
	postsConnection {
		edges {
			node {
				author {
					bio
					name
					id
					photo {
						url
					}
				}
				createdAt
				slug
				title
				excerpt
				featuredImage {
					url
				}
				categories {
					name
					slug
				}
			}
		}
	}
}`;

export const getRecentPosts = `{
	posts(
		orderBy: createdAt_ASC
		last: 3
	) {
		title
		featuredImage {
			url
		}
		createdAt
		slug
	}
}`;

export function getSimilarPosts(slug, categories) {
  return `
		query GetSimilarPosts {
			posts(where: {slug_not: "${slug}", AND: {categories_some: {slug_in: "${categories}"}}}, last: 3) {
				title
				featuredImage {
					url
					createdAt
				}
				slug
			}
		}
	`;
}

export const getCategories = `{
	categories {
		name
		slug
	}
}`;

export function getPostDetails(slug) {
  return `query GetPostDetails {
		post(where: {slug: "${slug}"}) {
			createdAt
			slug
			title
			excerpt
			author {
				bio
				name
				id
				photo {
					url
				}
			}
			featuredImage {
				url
			}
			categories {
				name
				slug
			}
			content {
				raw
			}
		}
	}`;
}

export function getPostsByCategory(slug) {
  return `query GetCategoryPost {
	postsConnection(where: {categories_some: {slug: "${slug}"}}) {
		edges {
			node {
				author {
					bio
					name
					id
					photo {
						url
					}
				}
				createdAt
				slug
				title
				excerpt
				featuredImage {
					url
				}
				categories {
					name
					slug
				}
			}
		}
	}
}`;
}
