import React, { useEffect, useState } from 'react';
import { Link } from 'react-router-dom';
import request from 'graphql-request';

import { getCategories } from '../../api/BlogApi';

import '../../assets/styles/BlogCategoriesWidget.css';

const CategoriesWidget = () => {
  const [categories, setCategories] = useState([]);
  const [loaded, setLoaded] = useState(false);

  const CATEGORIES_QUERY = getCategories;

  useEffect(() => {
    setLoaded(false);

    const fetchCategories = async () => {
      const { categories } = await request(
        process.env.REACT_APP_GRAPHCMS_ENDPOINT,
        CATEGORIES_QUERY
      );

      setCategories(categories);
      setLoaded(true);
    };

    fetchCategories();
  }, []);

  return (
    <>
      {loaded && (
        <div className='categories-container'>
          <h3 className='categories-title'>Categories</h3>

          {categories.map((category) => (
            <Link to={`/blog/category/${category.slug}`}>
              <span className='categories-link'>{category.name}</span>
            </Link>
          ))}
        </div>
      )}
    </>
  );
};

export default CategoriesWidget;
