import React, { useEffect, useState } from 'react';

import { motion } from 'framer-motion';

import ContainerHeading from '../components/UI/ContainerHeading';
import TestimonialCard from '../components/UI/TestimonialCard';

import { testimonialsData } from '../data/TestimonialsData';

import '../assets/styles/Testimonials.css';

const Testimonials = () => {
  const [data, setData] = useState(testimonialsData);
  const [index, setIndex] = useState(0);

  useEffect(() => {
    const lastIndex = data.length - 1;

    if (index < 0) {
      setIndex(lastIndex);
    }

    if (index > lastIndex) {
      setIndex(0);
    }
  }, [index, data]);

  useEffect(() => {
    let slider = setInterval(() => {
      setIndex(index + 1);
    }, 3000);

    return () => clearInterval(slider);
  }, [index]);

  return (
    <>
      <motion.section
        className='testimonials'
        id='testimonials'
        initial={{ opacity: 0 }}
        animate={{ opacity: 1 }}
        exit={{ opacity: 0 }}
      >
        <div className='container'>
          <ContainerHeading
            subtitle='WHAT PEOPLE SAY'
            title='Testimonials'
            center={true}
          />
        </div>

        <div className='slide'>
          {/* cards */}
          {testimonialsData.map((testimonial, testimonialIndex) => (
            <TestimonialCard
              key={testimonial.id}
              {...testimonial}
              valueIndex={testimonialIndex}
              index={index}
            />
          ))}

          {/* Button */}
          <div className='slide_button'>
            <button
              className='btn_shadow prev_btn'
              onClick={() => setIndex(index - 1)}
            >
              <i className='fas fa-arrow-left'></i>
            </button>
            <button
              className='btn_shadow next_btn'
              onClick={() => setIndex(index + 1)}
            >
              <i className='fas fa-arrow-right'></i>
            </button>
          </div>
        </div>
      </motion.section>
    </>
  );
};

export default Testimonials;
