import React from 'react';

// import { footerImage } from '../../assets/images/kyle.png';
import { kyleLogoImage } from '../../assets/images';

const Footer = () => {
  return (
    <>
      <footer>
        <div className='container text-center top'>
          <div className='img'>
            <img
              src={kyleLogoImage}
              width='50px'
              height='50px'
              style={{ borderRadius: '50%' }}
            />
          </div>

          <p>© 2022. All rights reserved by KyleKumar.</p>
        </div>
      </footer>
    </>
  );
};

export default Footer;
