import React from 'react';
import { Link } from 'react-router-dom';
import moment from 'moment';

import '../../assets/styles/BlogPostCard.css';

const PostCard = ({ post }) => {
  return (
    <div className='post-card-container'>
      <div className='post-card-image-container'>
        <img
          src={post.featuredImage.url}
          alt={post.title}
          className='post-card-image'
        />
      </div>

      <h1 className='post-card-title'>
        <Link to={`/post/${post.slug}`}>{post.title}</Link>
      </h1>

      <div className='post-details-container'>
        <div className='post-details-author-container'>
          <img
            src={post.author.photo.url}
            alt={post.author.name}
            height='30px'
            width='30px'
            className='post-details-author-image'
          />

          <p className='post-details-author-name'>{post.author.name}</p>
        </div>

        <div className='post-details-date-container'>
          <svg
            xmlns='http://www.w3.org/2000/svg'
            className='post-details-date-logo'
            fill='none'
            viewBox='0 0 24 24'
            stroke='currentColor'
          >
            <path
              strokeLinecap='round'
              strokeLinejoin='round'
              strokeWidth='2'
              d='M8 7V3m8 4V3m-9 8h10M5 21h14a2 2 0 002-2V7a2 2 0 00-2-2H5a2 2 0 00-2 2v12a2 2 0 002 2z'
            />
          </svg>

          <span>{moment(post.createdAt).format('DD MMM, YYYY')}</span>
        </div>
      </div>

      <p className='post-details-excerpt'>{post.excerpt}</p>

      <div className='post-details-button-container'>
        <Link to={`/post/${post.slug}`}>
          <span className='post-details-button'>Continue Reading</span>
        </Link>
      </div>
    </div>
  );
};

export default PostCard;
