import React, { useEffect, useState } from 'react';
import { Link } from 'react-router-dom';
import moment from 'moment';
import request from 'graphql-request';

import { getRecentPosts, getSimilarPosts } from '../../api/BlogApi';

import '../../assets/styles/BlogPostWidget.css';

const PostWidget = ({ postDetail }) => {
  const [widgetPosts, setWidgetPosts] = useState([]);
  const [loaded, setLoaded] = useState(false);

  const categories = postDetail?.categories.map((category) => category.slug);
  const slug = postDetail?.slug;

  // const RECENT_POSTS_QUERY = `
  // 	{
  // 		posts(
  // 			orderBy: createdAt_ASC
  // 			last: 3
  // 		) {
  // 			title
  // 			featuredImage {
  // 				url
  // 			}
  // 			createdAt
  // 			slug
  // 		}
  // 	}
  // `;

  const RECENT_POSTS_QUERY = getRecentPosts;
  const SIMILAR_POSTS_QUERY = getSimilarPosts(slug, categories);

  // const SIMILAR_POSTS_QUERY = `
  // 	query GetSimilarPosts {
  // 		posts(where: {slug_not: "${slug}", AND: {categories_some: {slug_in: "${categories}"}}}, last: 3) {
  // 			title
  // 			featuredImage {
  // 				url
  // 				createdAt
  // 			}
  // 			slug
  // 		}
  // 	}
  // `;

  useEffect(() => {
    setLoaded(false);

    const fetchSimilarPosts = async () => {
      const { posts } = await request(
        process.env.REACT_APP_GRAPHCMS_ENDPOINT,
        SIMILAR_POSTS_QUERY
      );

      setWidgetPosts(posts);
      setLoaded(true);
    };

    const fetchRecentPosts = async () => {
      const { posts } = await request(
        process.env.REACT_APP_GRAPHCMS_ENDPOINT,
        RECENT_POSTS_QUERY
      );

      setWidgetPosts(posts);
      setLoaded(true);
    };

    if (slug) {
      // get similar posts as slug provided
      fetchSimilarPosts();
    } else {
      // get recent posts as no slug provided
      fetchRecentPosts();
    }
  }, [slug]);

  return (
    <>
      {loaded && (
        <div className='post-widget-container'>
          <h3 className='post-widget-title'>
            {slug ? 'Related Posts' : 'Recent Posts'}
          </h3>

          {widgetPosts.map((post) => (
            <div className='post-widget-card-container'>
              <div className='post-widget-card-image-container'>
                <img
                  src={post.featuredImage.url}
                  alt={post.title}
                  height='60px'
                  width='60px'
                  className='post-widget-card-image'
                />
              </div>

              <div className='post-widget-details-container'>
                <p className='post-widget-details-date'>
                  {moment(post.createdAt).format('DD MMM, YYYY')}
                </p>

                <Link
                  to={`/post/${post.slug}`}
                  key={post.title}
                  className='post-widget-details-title'
                >
                  {post.title}
                </Link>
              </div>
            </div>
          ))}
        </div>
      )}
    </>
  );
};

export default PostWidget;
