import React, { useEffect, useState } from 'react';
import { useParams } from 'react-router-dom';
import request from 'graphql-request';

import PostWidget from '../components/Blog/PostWidget';
import CategoriesWidget from '../components/Blog/CategoriesWidget';
import PostDetail from '../components/Blog/PostDetail';

import { getPostDetails } from '../api/BlogApi';

import '../assets/styles/BlogPost.css';

const BlogPost = () => {
  const { slug } = useParams();
  const [postDetail, setPostDetail] = useState([]);
  const [loaded, setLoaded] = useState(false);

  const POST_DETAILS_QUERY = getPostDetails(slug);

  useEffect(() => {
    setLoaded(false);

    const fetchPostDetails = async () => {
      const { post } = await request(
        process.env.REACT_APP_GRAPHCMS_ENDPOINT,
        POST_DETAILS_QUERY
      );

      setPostDetail(post);
      setLoaded(true);
    };

    fetchPostDetails();
  }, []);

  return (
    <div className='blog-post-container'>
      <div className='blog-post-grid'>
        <div className='blog-post-detail-container'>
          {loaded && (
            <PostDetail
              featuredImageUrl={postDetail.featuredImage.url}
              title={postDetail.title}
              authorImageUrl={postDetail.author.photo.url}
              authorName={postDetail.author.name}
              postCreatedAt={postDetail.createdAt}
              postContent={postDetail.content.raw.children}
            />
          )}
        </div>

        <div className='blog-post-aside-container'>
          <div className='blog-post-aside-inner'>
            {loaded && <PostWidget postDetail={postDetail} />}
            <CategoriesWidget />
          </div>
        </div>
      </div>
    </div>
  );
};

export default BlogPost;
