import React from 'react';

import { motion } from 'framer-motion';

import ContainerHeading from '../components/UI/ContainerHeading';
import ExperienceCard from '../components/UI/ExperienceCard';

import { experienceData } from '../data/ExperienceData';

import '../assets/styles/Experience.css';

const Experience = () => {
  return (
    <>
      <motion.section
        className='experience'
        id='experience'
        initial={{ opacity: 0 }}
        animate={{ opacity: 1 }}
        exit={{ opacity: 0 }}
      >
        <div className='container top'>
          <ContainerHeading
            subtitle='My Experience'
            title='My Experience'
            center={true}
          />

          <div className='content-section mtop d_flex'>
            <div className='left'>
              <div className='heading'>
                <h4>2007-2010</h4>
                <h1 style={{ fontSize: '40px' }}>My Education</h1>
              </div>

              <div className='content'>
                {experienceData.map((experience, id) => {
                  if (experience.category === 'education') {
                    return (
                      <ExperienceCard
                        key={id}
                        title={experience.title}
                        year={experience.year}
                        rate={experience.rate}
                        description={experience.desc}
                      />
                    );
                  }
                })}
              </div>
            </div>

            <div className='left'>
              <div className='heading'>
                <h4>2007-2010</h4>
                <h1 style={{ fontSize: '40px' }}>My Job Experience</h1>
              </div>

              <div className='content'>
                {experienceData.map((experience, id) => {
                  if (experience.category === 'experience') {
                    return (
                      <ExperienceCard
                        key={id}
                        title={experience.title}
                        year={experience.year}
                        rate={experience.rate}
                        description={experience.desc}
                      />
                    );
                  }
                })}
              </div>
            </div>
          </div>
        </div>
      </motion.section>
    </>
  );
};

export default Experience;
