import React from 'react';

import Typewriter from 'typewriter-effect';
import { motion } from 'framer-motion';

import { typewriterWords } from '../../data/HeroTypewriterData';

import { kyleLogoImage } from '../../assets/images/';

import '../../assets/styles/Hero.css';

const Hero = () => {
  return (
    <>
      <section className='hero' id='home'>
        <div className='container f_flex top'>
          <div className='left top'>
            <h3>WELCOME TO MY WORLD</h3>
            <h1>
              Hi, I'm <span>Kyle</span>
            </h1>
            <h2>
              a {``}
              <span>
                <Typewriter
                  options={{
                    strings: typewriterWords,
                    autoStart: true,
                    loop: true,
                    pauseFor: 1000,
                    deleteSpeed: 50,
                  }}
                />
              </span>
            </h2>

            <p>
              I use animation as a third dimension by which to simplify
              experiences and kuiding through each and every interaction. I’m
              not adding motion just to spruce things up, but doing it in ways
              that.
            </p>
          </div>

          <div className='right'>
            <div className='right_img'>
              <img src={kyleLogoImage} alt='' />
            </div>
          </div>
        </div>
      </section>
    </>
  );
};

export default Hero;
