import React from 'react';

const FeaturesCard = ({ image, title, description }) => {
  return (
    <>
      <div className='box btn_shadow'>
        <img src={image} alt={`${title} image`} />
        <h2>{title}</h2>
        <p>{description}</p>
        <a href='/'>
          <i className='fas fa-arrow-right'></i>
        </a>
      </div>
    </>
  );
};

export default FeaturesCard;
