import React from 'react';
import moment from 'moment';

import { getContentFragment } from '../../utils/getContentFragment';

import '../../assets/styles/BlogPostDetail.css';

const PostDetail = ({
  featuredImageUrl,
  title,
  authorImageUrl,
  authorName,
  postCreatedAt,
  postContent,
}) => {
  console.log(postContent);

  return (
    <div className='post-detail-container'>
      <div className='post-detail-image-container'>
        <img src={featuredImageUrl} alt={title} className='post-detail-image' />
      </div>

      <div className='post-detail-content-container'>
        <div className='post-detail-content-inner'>
          <div className='post-detail-content-author-container'>
            <img
              src={authorImageUrl}
              alt={authorName}
              height='30px'
              width='30px'
              className='post-detail-content-author-image'
            />

            <p className='post-detail-content-author-name'>{authorName}</p>
          </div>

          <div className='post-detail-content-date-container'>
            <svg
              xmlns='http://www.w3.org/2000/svg'
              className='post-detail-content-date-logo'
              fill='none'
              viewBox='0 0 24 24'
              stroke='currentColor'
            >
              <path
                strokeLinecap='round'
                strokeLinejoin='round'
                strokeWidth='2'
                d='M8 7V3m8 4V3m-9 8h10M5 21h14a2 2 0 002-2V7a2 2 0 00-2-2H5a2 2 0 00-2 2v12a2 2 0 002 2z'
              />
            </svg>

            <span>{moment(postCreatedAt).format('DD MMM, YYYY')}</span>
          </div>
        </div>

        {/* content */}
        <h1 className='post-detail-content-title'>{title}</h1>
        {/* function to load content */}
        {postContent.map((typeObj, index) => {
          const children = typeObj.children.map((item, itemIndex) =>
            getContentFragment(itemIndex, item.text, item)
          );

          return getContentFragment(index, children, typeObj, typeObj.type);
        })}
      </div>
    </div>
  );
};

export default PostDetail;
