import React from 'react';

const FilterComponent = ({ setActiveCategory, activeCategory }) => {
  return (
    <div className='filter-container'>
      <button
        className={`filter-container__button ${
          activeCategory === 'React JS' ? 'active' : ''
        }`}
        onClick={() => setActiveCategory('React JS')}
      >
        React JS
      </button>
      <button
        className={`filter-container__button ${
          activeCategory === 'Functional Websites' ? 'active' : ''
        }`}
        onClick={() => setActiveCategory('Functional Websites')}
      >
        Functional Websites
      </button>
      <button
        className={`filter-container__button ${
          activeCategory === 'UI/UX Designs' ? 'active' : ''
        }`}
        onClick={() => setActiveCategory('UI/UX Designs')}
      >
        UI/UX Designs
      </button>
    </div>
  );
};

export default FilterComponent;
