import React from 'react';

import { motion } from 'framer-motion';

import Hero from './home/Hero';
import Skills from './home/Skills';

const Home = () => {
  return (
    <motion.div
      className='page'
      initial={{ opacity: 0 }}
      animate={{ opacity: 1 }}
      exit={{ opacity: 0 }}
    >
      <Hero />
      <Skills />
    </motion.div>
  );
};

export default Home;
