import React from 'react';

export function getContentFragment(index, text, obj, type) {
  let modifiedText = text;

  if (obj) {
    if (obj.bold) {
      modifiedText = <b key={index}>{text}</b>;
    }

    if (obj.italic) {
      modifiedText = <em key={index}>{text}</em>;
    }

    if (obj.underline) {
      modifiedText = <u key={index}>{text}</u>;
    }
  }

  switch (type) {
    case 'heading-two':
      return (
        <h2
          key={index}
          style={{
            fontSize: '1.5rem',
            lineHeight: '2rem',
            fontWeight: '600',
            marginTop: '2rem',
            marginBottom: '1rem',
          }}
          className='text-2xl font-semibold mb-4'
        >
          {modifiedText.map((item, i) => (
            <React.Fragment key={i}>{item}</React.Fragment>
          ))}
        </h2>
      );
    case 'heading-three':
      return (
        <h3
          key={index}
          style={{
            marginTop: '2rem',
            marginBottom: '1rem',
            fontSize: '1.25rem',
            lineHeight: '1.75rem',
            fontWeight: '600',
          }}
        >
          {modifiedText.map((item, i) => (
            <React.Fragment key={i}>{item}</React.Fragment>
          ))}
        </h3>
      );
    case 'paragraph': {
      return (
        <p key={index} style={{ marginBottom: '1rem' }}>
          {modifiedText.map((item, i) => (
            <React.Fragment key={i}>{item}</React.Fragment>
          ))}
        </p>
      );
    }

    case 'heading-four':
      return (
        <p key={index} style={{ marginBottom: '1rem' }}>
          {modifiedText.map((item, i) => (
            <React.Fragment key={i}>{item}</React.Fragment>
          ))}
        </p>
      );
    case 'image':
      return (
        <img
          key={index}
          alt={obj.title}
          height={obj.height}
          width={obj.width}
          src={obj.src}
        />
      );
    default:
      return modifiedText;
  }
}
