import React, { useState } from 'react';
import { NavLink as Link } from 'react-router-dom';

import '../../assets/styles/Header.css';

const Header = ({ activeMobile, setActiveMobile }) => {
  // toggle header from being 'fixed' based on scroll position
  window.addEventListener('scroll', function () {
    // const header = document.querySelector('.header');
    // header.classList.toggle('active', window.scrollY > 100);
  });

  return (
    <>
      <header className='header'>
        <div className='container d_flex'>
          <div className='logo'>KyleKumar</div>

          <div className='nav-links-container'>
            <ul
              className={
                activeMobile
                  ? 'nav-links-mobile'
                  : 'link nav-links f_flex uppercase'
              }
              onClick={() => setActiveMobile(false)}
            >
              <li>
                {/* <a href='#home'>home</a> */}
                <Link to='/' activeClassName='nav-links_active'>
                  home
                </Link>
              </li>
              {/* <li>
                <a href='#skills'>skills</a>
              </li> */}
              <li>
                <Link to='/portfolio' activeStyle={{ color: 'red' }}>
                  portfolio
                </Link>
              </li>
              <li>
                <Link to='/experience'>experience</Link>
              </li>
              <li>
                <Link to='/blog'>blog</Link>
              </li>
              <li>
                <Link to='/testimonials'>testimonials</Link>
              </li>

              {/* <li>
                <a href='#contact'>contact</a>
              </li> */}
              {/* <li>
                <button className='home-btn'>BUY NOW</button>
              </li> */}
            </ul>

            <button
              className='toggle'
              onClick={() => setActiveMobile(!activeMobile)}
            >
              {activeMobile ? (
                <i className='fas fa-times close home-btn'></i>
              ) : (
                <i className='fas fa-bars open'></i>
              )}
            </button>
          </div>
        </div>
      </header>
    </>
  );
};

export default Header;
