import React, { useEffect, useState } from 'react';

import { motion, AnimatePresence } from 'framer-motion';

import FilterComponent from '../components/Filter/FilterComponent';
import PortfolioCard from '../components/UI/PortfolioCard';

// import { portfolioData } from '../../data/PortfolioData';
import { reactPortfolioData } from '../data/portfolio/ReactPortfolioData';
import { websitesPortfolioData } from '../data/portfolio/WebsitesPortfolioData';
import { uiUxPortfolioData } from '../data/portfolio/UiUxPortfolioData';

import '../assets/styles/Portfolio.css';

const Portfolio = () => {
  const [activeCategory, setActiveCategory] = useState('React JS');
  const [portfolioData, setPortfolioData] = useState([]);

  useEffect(() => {
    if (activeCategory === 'React JS') {
      setPortfolioData(reactPortfolioData);
      return;
    }

    if (activeCategory === 'Functional Websites') {
      setPortfolioData(websitesPortfolioData);
      return;
    }

    if (activeCategory === 'UI/UX Designs') {
      setPortfolioData(uiUxPortfolioData);
      return;
    }
  }, [activeCategory]);

  return (
    <>
      <section className='portfolio top' id='portfolio'>
        <div className='container'>
          <div className='heading text-center'>
            <h4>VISIT MY PORTFOLIO AND KEEP YOUR FEEDBACK</h4>
            <h1>My Portfolio</h1>
            <p>
              View a live demo and view the source code for any of these
              projects my clicking on the buttons.
            </p>
            <p>Click on the card to view more details about each project.</p>
          </div>

          <FilterComponent
            activeCategory={activeCategory}
            setActiveCategory={setActiveCategory}
          />

          <motion.div layout className='content grid'>
            {/* {portfolioData.map((portfolioItem, index) => (
              <PortfolioCard
                key={index}
                image={portfolioItem.image}
                category={portfolioItem.category}
                totalLike={portfolioItem.totalLike}
                title={portfolioItem.title}
              />
            ))} */}
            {/* {reactPortfolioData.map((portfolioItem, index) => (
              <PortfolioCard
                key={index}
                image={portfolioItem.image}
                title={portfolioItem.title}
                description={portfolioItem.description}
                category={portfolioItem.category}
                projectLink={portfolioItem.projectLink}
                codeLink={portfolioItem.codeLink}
              />
            ))} */}

            <AnimatePresence exitBeforeEnter>
              {portfolioData.map((portfolioItem, index) => (
                <PortfolioCard
                  key={index}
                  image={portfolioItem.image}
                  title={portfolioItem.title}
                  description={portfolioItem.description}
                  category={portfolioItem.category}
                  projectLink={portfolioItem.projectLink}
                  codeLink={portfolioItem.codeLink}
                />
              ))}
            </AnimatePresence>
          </motion.div>
        </div>
      </section>
    </>
  );
};

export default Portfolio;
