import request from 'graphql-request';
import React, { useEffect, useState } from 'react';
import { useParams } from 'react-router-dom';
import { getPostsByCategory } from '../api/BlogApi';

import PostCard from '../components/Blog/PostCard';
import CategoriesWidget from '../components/Blog/CategoriesWidget';

import '../assets/styles/BlogCategories.css';

const BlogCategory = () => {
  const { slug } = useParams();
  const [posts, setPosts] = useState([]);
  const [loaded, setLoaded] = useState(false);

  const POSTS_BY_CATEGORY_QUERY = getPostsByCategory(slug);

  useEffect(() => {
    setLoaded(false);

    const fetchPosts = async () => {
      const { postsConnection } = await request(
        process.env.REACT_APP_GRAPHCMS_ENDPOINT,
        POSTS_BY_CATEGORY_QUERY
      );

      setPosts(postsConnection.edges);
      setLoaded(true);
    };

    fetchPosts();
  }, [slug]);

  return (
    <div className='blog-categories-container'>
      <div className='blog-categories-title-container'>
        <h1 className='blog-categories-title'>Category - "{slug}"</h1>
      </div>

      <div className='blog-categories-posts-grid'>
        {loaded && (
          <div className='blog-categories-posts-container'>
            {posts.map((post) => (
              <PostCard post={post.node} />
            ))}
          </div>
        )}

        <div className='blog-categories-widgets-container'>
          <div className='blog-categories-widgets-inner'>
            <CategoriesWidget />
          </div>
        </div>
      </div>
    </div>
  );
};

export default BlogCategory;
