import React, { useEffect, useState } from 'react';
import request from 'graphql-request';

import PostCard from '../components/Blog/PostCard';

import { getPosts } from '../api/BlogApi';

import '../assets/styles/Blog.css';
import PostWidget from '../components/Blog/PostWidget';
import CategoriesWidget from '../components/Blog/CategoriesWidget';

const Blog = () => {
  const [posts, setPosts] = useState([]);
  const [loaded, setLoaded] = useState(false);

  useEffect(() => {
    setLoaded(false);

    const fetchPosts = async () => {
      const { postsConnection } = await request(
        process.env.REACT_APP_GRAPHCMS_ENDPOINT,
        getPosts
      );

      setPosts(postsConnection.edges);
      setLoaded(true);
    };

    fetchPosts();
  }, []);

  return (
    <div className='blog-container'>
      <div className='blog-grid'>
        {loaded && (
          <div className='blog-grid-card-container'>
            {posts.map((post) => (
              <PostCard post={post.node} key={post.node.title} />
            ))}
          </div>
        )}

        <div className='blog-side-container'>
          <div className='blog-side-inner'>
            <PostWidget />
            <CategoriesWidget />
          </div>
        </div>
      </div>
    </div>
  );
};

export default Blog;
