import React, { useState } from 'react';

import { motion } from 'framer-motion';

import '../../assets/styles/Portfolio.css';

const PortfolioCard = ({
  image,
  category,
  totalLike,
  title,
  description,
  projectLink,
  codeLink,
}) => {
  const [modal, setModal] = useState(false);

  const toggleModal = () => {
    setModal((prevModal) => !prevModal);
  };

  if (modal) {
    document.body.classList.add('active-modal');
  } else {
    document.body.classList.remove('active-modal');
  }

  return (
    <motion.div
      layout
      animate={{ opacity: 1, x: 0 }}
      initial={{ opacity: 0, x: '-100%' }}
      exit={{ opacity: 0, x: '100%' }}
      transition={{ duration: 0.5 }}
    >
      <div className='box btn_shadow'>
        <div className='img' onClick={toggleModal}>
          <img src={image} alt='' onClick={toggleModal} />
        </div>

        <div className='category d_flex' onClick={toggleModal}>
          <span onClick={toggleModal}>{category}</span>
          <label>{/* <i className='far fa-heart'></i> */}</label>
        </div>

        <div className='title' onClick={toggleModal}>
          <h2 onClick={toggleModal}>{title}</h2>
          {/* <a href='#popup' className='arrow' onClick={toggleModal}>
            <i className='fas fa-arrow-right'></i>
          </a> */}
        </div>

        <div className='description'>
          <p>{description}</p>
        </div>

        <div className='buttons'>
          <a href={projectLink} target='_blank'>
            <button className='btn_shadow'>Live Demo</button>
          </a>

          <a href={codeLink} target='_blank'>
            <button className='btn_shadow'>View Code</button>
          </a>
        </div>
      </div>

      {/* Popup Box */}

      {modal && (
        <div className='modal'>
          <div onClick={toggleModal} className='overlay'></div>
          <div className='modal-content d_flex'>
            <div className='modal-img left'>
              <img src={image} alt='' />
            </div>
            <div className='modal-text right'>
              <span>Featured - Design</span>
              <h1>{title}</h1>
              <p>
                Lorem ipsum dolor sit amet consectetur adipisicing elit.
                Cupiditate distinctio assumenda explicabo veniam temporibus
                eligendi.
              </p>
              <p>
                Consectetur adipisicing elit. Cupiditate distinctio assumenda.
                dolorum alias suscipit rerum maiores aliquam earum odit, nihil
                culpa quas iusto hic minus!
              </p>
              <div className='button f_flex mtop'>
                {/* <button className='btn_shadow'>
                  LIKE THIS <i class='far fa-thumbs-up'></i>
                </button> */}
                <button className='btn_shadow'>
                  VIEW PROJECT<i class='fas fa-chevron-right'></i>
                </button>
              </div>
              <button className='close-modal btn_shadow' onClick={toggleModal}>
                <i class='fas fa-times'></i>
              </button>
            </div>
          </div>
        </div>
      )}
    </motion.div>
  );
};

export default PortfolioCard;
